$text-color: hsl(0, 0%, 10%) !default;
$border-color: hsl(0, 0%, 60%) !default;
$transition-time: 0.3s !default;

.menu {
  position: absolute;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 10;
  top: 120%;
  left: 0;
  width: auto;
  min-width: 10em;  //   
  max-width: 90vw;
  background-color: #fff;
  color: $text-color;
  border: 1px solid $border-color;
  border-radius: 3px;
  opacity: 0;  //    
  visibility: hidden;  //  
  overflow: hidden; //      
  transition: opacity $transition-time, visibility $transition-time, max-height $transition-time;

  //    
  &__item {
    position: relative;
    cursor: pointer;

    &--separator {
      &:not(:last-child) {
        padding-bottom: 0.3em;
        margin-bottom: 0.3em;
        border-bottom: 1px solid $border-color;
      }
    }

    //    
    &:hover {
      & > .menu__lvl--2 {
        opacity: 1;
        visibility: visible;
        max-height: 1000px;
        overflow: visible;
      }
    }
  }

  &__link {
    display: block;
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0.3em 1em;
    color: $text-color;
    text-decoration: none;

    &:hover,
    &:focus {
      color: darken($text-color, 10%);
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  // ,       
  &__lvl--2 {
    margin: 0;
    padding: 0;
    list-style: none;
    position: absolute;
    top: 0;  //     
    left: 100%;  //    
    z-index: 1;
    opacity: 0;  //   
    visibility: hidden; //   
    max-height: 0;  // 
    overflow: hidden;
    transition: opacity $transition-time, visibility $transition-time, max-height $transition-time;

    //  ,   
    background-color: #fff;
    border: 1px solid $border-color;
    border-radius: 3px;
    width: auto;
    min-width: 10em;
  }

  //     
  &.show {
    opacity: 1;
    visibility: visible;
    max-height: 1000px;
    overflow: visible;
  }
}
