$color-danger:                hsl(2, 64%, 58%) !default;

.field-select {

  $block-name:                &;

  &--error {

    #{$block-name}__name,
    #{$block-name}__select,
    #{$block-name}__help-text {
      color: $color-danger;
    }

    #{$block-name}__select {
      border-color: $color-danger;
      background-color: lighten($color-danger, 35%);
    }
  }
}
